@media (prefers-color-scheme: dark) {
  body   {   background: -webkit-linear-gradient(130deg, #232526, #414345); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(130deg, #232526, #414345);
  color: aliceblue; }
}

@media (prefers-color-scheme: light) {
  body   { background: -webkit-linear-gradient(to right, #efefbb, #d4d3dd); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #efefbb, #d4d3dd);
  color: #232526;}
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  width: 100%;
  min-height: 100vh;
  font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  display: grid;
  place-items: center;
}
.App{
  width: 100%;
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.App h1{
  font-size: 4rem;
  letter-spacing: 4px;
  text-shadow: 3px 3px 0 #640ec7,
  6px 6px 0 #cadf0e;
}
.App img{
  width: 90%;
  height: 60vh;
  object-fit: 100%;
  object-position: center;
}
@media screen and (min-width:520px) {
  .App img{
    object-fit:contain;
  }
}
.App p{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  padding: 0 1em;
  font-weight: 600;
}